body{
  font-family: "Circular";
  margin: 0;
  font-size: 1rem;
  background-image: url('./img/seebaly.svg');
  background-size: cover;
  width: 100%;
  height: 700px;
  background-position: right;
  position:relative;
  background-repeat: no-repeat;
}

@font-face {
  font-family: "Circular";
  font-style: normal;
  src: url("https://sp-bootstrap.global.ssl.fastly.net/8.2.4/fonts/circular-book.woff2") format("woff2"), url("https://sp-bootstrap.global.ssl.fastly.net/8.2.4/fonts/circular-book.woff") format("woff");
}
.header{
  background-color: rgba(46, 78, 183, 0);
  font-weight: 100;
}
.font1 {
  color: white;
}
.space {
  margin-top: 70px;
  
}
.space1{
  margin-bottom: 120px;
}

.btn0 {
  /*background: linear-gradient(to left, #00CCE4, #4643F6 );*/
  background: -webkit-gradient(linear, right top, left top, from(rgb(77, 127, 221)), to(rgb(63, 136, 219)) );
  background: -webkit-linear-gradient(right, rgb(77, 127, 221), rgb(63, 136, 219) );
  background: -o-linear-gradient(right, rgb(77, 127, 221), rgb(63, 136, 219) );
  background: linear-gradient(to left, rgb(77, 127, 221), rgb(63, 136, 219) );
  
  color: rgb(255, 255, 255);
  -webkit-box-shadow: 0 7px 14px 0 rgba(49,49,93,0.10),
              0 3px 6px 0 rgba(0,0,0,0.08);
          box-shadow: 0 7px 14px 0 rgba(49,49,93,0.10),
              0 3px 6px 0 rgba(0,0,0,0.08);
  border-radius: 30px;
  border: 0;

  font-size: 20px;
  font-weight: 400;
  line-height: 38px;
  outline: none;
  cursor: pointer;
}
.text-gradient {
  color: #f5f5f5 ;
}
.bubble {
  margin: 10px 0 10px 0;
  background: #D6F8C9;
  border-radius: 20px;
  padding: 10px;
  -webkit-box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
          box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
}
.bubble1 {
  background: white;
  border-radius: 20px;
  padding: 10px;
  -webkit-box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
          box-shadow: 0 5px 10px 1px rgba(28, 28, 39, 0.1);
}
.sect2-margin {
  padding-bottom: 274px;
}

.foo {
  padding-bottom: 3px;
}
.foo p {
  font-weight:400;
  color: black;
}

/*Responsive */

@media (max-width: 1920px) {
  .font1 {
    font-size: 50px;
  }
}

@media (max-width: 992px) {
  .font1 {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .font1 {
    font-size: 30px;
  }
  .space {
    margin-top: 80px;
  }
}

@media (max-width: 575px) {
  .font1 {
    font-size: 20px;
  }
  .space {
    margin-top: 90px;
  }
}

/* ANIMATIONS */
@keyframes fadeInUp {
  from {
      -webkit-transform: translate3d(0,40px,0);
              transform: translate3d(0,40px,0)
  }

  to {
      -webkit-transform: translate3d(0,0,0);
              transform: translate3d(0,0,0);
      opacity: 1
  }
}

@-webkit-keyframes fadeInUp {
  from {
      -webkit-transform: translate3d(0,40px,0);
              transform: translate3d(0,40px,0)
  }

  to {
      -webkit-transform: translate3d(0,0,0);
              transform: translate3d(0,0,0);
      opacity: 1
  }
}

.animated {
  animation-duration: 2s;
  animation-fill-mode: both;
  -webkit-animation-duration: 2s;
  -webkit-animation-fill-mode: both
}

.animatedFadeInUp {
  opacity: 0
}

.fadeInUp {
  opacity: 0;
  animation-name: fadeInUp;
  -webkit-animation-name: fadeInUp;
}   
/* END ANIMATIONS */



